import { Component, OnInit } from '@angular/core';
import { ProductService } from "../../../../services/products.service";
import { Router } from "@angular/router";
import { CategoryModel } from "./category.model";
@Component({
    selector: 'category',
    template: require('./category.component.html'),
    styles: [require('./category.component.css')]
})


export class CategoryComponent implements OnInit {
    category: CategoryModel
    categories: any[]
    cate: any;
    ngOnInit(): void {
        this.getCategories();
    }
    updateData(){
        this.getCategories();
    }
    constructor(private _products: ProductService, private router: Router) { }
    getCategories() {
        this._products.getCategories().subscribe(result => this.categories = result);
    }
    categoryDetail(cate: any) {
        this.cate = cate;
    }
    addCategoryModel() {
        this.category = new CategoryModel("", "");
    }
    saveChanges(category: any) {
        this._products.addCategory(category).subscribe(result => {
            if (result) {
                this.getCategories();
            }
        })
    }
}




